// Copy pasted from: https://github.com/InseeFrLab/keycloakify/blob/main/src/login/Template.tsx

import {useState} from "react";
import {assert} from "keycloakify/tools/assert";
import {clsx} from "keycloakify/tools/clsx";
import {usePrepareTemplate} from "keycloakify/lib/usePrepareTemplate";
import {type TemplateProps} from "keycloakify/login/TemplateProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "./kcContext";
import type {I18n} from "./i18n";
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger
} from "components/ui/dropdown-menu";
import {buttonVariants} from "components/ui/button";
import {cn} from "lib/utils";
import {Alert, AlertDescription, AlertTitle} from "../components/ui/alert";
import {ExclamationTriangleIcon} from "@radix-ui/react-icons";
import {Card, CardContent} from "../components/ui/card";
import {Boxes} from "../components/ui/background-boxes";
import {Meteors} from "../components/ui/meteors";
import {WavyBackground} from "../components/ui/wavy-background";
import {BackgroundBeams} from "../components/ui/background-beams";

export default function Template(props: TemplateProps<KcContext, I18n>) {
    const {
        displayInfo = false,
        displayMessage = true,
        displayRequiredFields = false,
        displayWide = false,
        showAnotherWayIfPresent = true,
        headerNode,
        showUsernameNode = null,
        infoNode = null,
        kcContext,
        i18n,
        doUseDefaultCss,
        classes,
        children
    } = props;

    const {getClassName} = useGetClassName({doUseDefaultCss, classes});

    const {msg, changeLocale, labelBySupportedLanguageTag, currentLanguageTag} = i18n;

    const {realm, locale, auth, url, message, isAppInitiatedAction} = kcContext;

    const {isReady} = usePrepareTemplate({
        "doFetchDefaultThemeResources": doUseDefaultCss,
        "styles": [
            // `${url.resourcesCommonPath}/node_modules/patternfly/dist/css/patternfly.min.css`,
            // `${url.resourcesCommonPath}/node_modules/patternfly/dist/css/patternfly-additions.min.css`,
            // `${url.resourcesCommonPath}/lib/zocial/zocial.css`,
            // `${url.resourcesPath}/css/login.css`
        ],
        "htmlClassName": getClassName("kcHtmlClass"),
        "bodyClassName": getClassName("kcBodyClass")
    });

    useState(() => {
        document.title = i18n.msgStr("loginTitle", kcContext.realm.displayName);
    });

    if (!isReady) {
        return null;
    }
    console.log(kcContext)
    return (
        <div >
            <div
                className="h-screen relative w-full overflow-hidden flex flex-col items-center justify-center">
                <BackgroundBeams />
                {realm.internationalizationEnabled && (assert(locale !== undefined), true) && locale.supported.length > 1 && (
                    <DropdownMenu>
                        <DropdownMenuTrigger className={cn(
                            "z-10",
                            buttonVariants({variant: "ghost"}),
                            "absolute right-4 top-4 md:right-8 md:top-8"
                        )}> {labelBySupportedLanguageTag[currentLanguageTag]}</DropdownMenuTrigger>
                        <DropdownMenuContent>
                            {locale.supported.map(({languageTag}) => (
                                <DropdownMenuItem
                                    onClick={() => changeLocale(languageTag)}>  {labelBySupportedLanguageTag[languageTag]}</DropdownMenuItem>
                            ))}
                        </DropdownMenuContent>
                    </DropdownMenu>
                )}
                <div className="absolute right-4 top-4 md:left-8 md:top-8 flex items-center z-20">
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        className="mr-2 h-6 w-6"
                    >
                        <path d="M15 6v12a3 3 0 1 0 3-3H6a3 3 0 1 0 3 3V6a3 3 0 1 0-3 3h12a3 3 0 1 0-3-3"/>
                    </svg>
                    {msg("loginTitleHtml", realm.displayNameHtml)}
                </div>
                <div
                    className="container relative h-screen flex items-center justify-center md:grid lg:max-w-none lg:px-0">
                    <div className="mx-auto flex w-full flex-col justify-center space-y-6 items-center">
                        <div className="flex space-y-2 text-center">
                            {!(auth !== undefined && auth.showUsername && !auth.showResetCredentials) ? (
                                displayRequiredFields ? (
                                    <div className={getClassName("kcContentWrapperClass")}>
                                        <div className={clsx(getClassName("kcLabelWrapperClass"), "subtitle")}>
                                        </div>
                                        <div className="col-md-10">
                                            <h1 className={"text-2xl font-semibold tracking-tight"}
                                                id="kc-page-title">{headerNode}</h1>
                                        </div>
                                    </div>
                                ) : (
                                    <h1 className={"text-2xl font-semibold tracking-tight"}
                                        id="kc-page-title">{headerNode}</h1>
                                )
                            ) : displayRequiredFields ? (
                                <div className={getClassName("kcContentWrapperClass")}>
                                    <div className={clsx(getClassName("kcLabelWrapperClass"), "subtitle")}>
                                <span className="subtitle">
                                    <span className="required">*</span> {msg("requiredFields")}
                                </span>
                                    </div>
                                    <div className="col-md-10">
                                        {showUsernameNode}
                                        <div className={getClassName("kcFormGroupClass")}>
                                            <div id="kc-username">
                                                <label id="kc-attempted-username">{auth?.attemptedUsername}</label>
                                                <a id="reset-login" href={url.loginRestartFlowUrl}>
                                                    <div className="kc-login-tooltip">
                                                        <i className={getClassName("kcResetFlowIcon")}></i>
                                                        <span
                                                            className="kc-tooltip-text">{msg("restartLoginTooltip")}</span>
                                                    </div>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <>
                                    {showUsernameNode}
                                    <div className={getClassName("kcFormGroupClass")}>
                                        <div id="kc-username">
                                            <label id="kc-attempted-username">{auth?.attemptedUsername}</label>
                                            <a id="reset-login" href={url.loginRestartFlowUrl}>
                                                <div className="kc-login-tooltip">
                                                    <i className={getClassName("kcResetFlowIcon")}></i>
                                                    <span
                                                        className="kc-tooltip-text">{msg("restartLoginTooltip")}</span>
                                                </div>
                                            </a>
                                        </div>
                                    </div>
                                </>
                            )}
                        </div>
                        <Card id="kc-content" className={"z-20"}>
                            <CardContent id="kc-content-wrapper" className={"flex flex-col min-[w]:350px"}>
                                {/* App-initiated actions should not see warning messages about the need to complete the action during login. */}
                                {displayMessage && message !== undefined && (message.type !== "warning" || !isAppInitiatedAction) && (
                                    <div className={clsx("alert", `alert-${message.type}`)}>
                                        {message.type === "success" &&
                                            <span className={getClassName("kcFeedbackSuccessIcon")}></span>}
                                        {message.type === "warning" &&
                                            <span className={getClassName("kcFeedbackWarningIcon")}></span>}
                                        {message.type === "error" &&
                                            <Alert variant="destructive" className={"mt-3"}>
                                                <ExclamationTriangleIcon className="h-4 w-4"/>
                                                <AlertDescription>
                                                    {message.summary}
                                                </AlertDescription>
                                            </Alert>
                                        }
                                        {message.type === "info" &&
                                            <span className={getClassName("kcFeedbackInfoIcon")}></span>}
                                    </div>
                                )}
                                {children}
                                {auth !== undefined && auth.showTryAnotherWayLink && showAnotherWayIfPresent && (
                                    <form
                                        id="kc-select-try-another-way-form"
                                        action={url.loginAction}
                                        method="post"
                                        className={clsx(displayWide && getClassName("kcContentWrapperClass"))}
                                    >
                                        <div
                                            className={clsx(
                                                displayWide && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                                            )}
                                        >
                                            <div className={getClassName("kcFormGroupClass")}>
                                                <input type="hidden" name="tryAnotherWay" value="on"/>
                                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                                <a
                                                    href="#"
                                                    id="try-another-way"
                                                    onClick={() => {
                                                        document.forms["kc-select-try-another-way-form" as never].submit();
                                                        return false;
                                                    }}
                                                >
                                                    {msg("doTryAnotherWay")}
                                                </a>
                                            </div>
                                        </div>
                                    </form>
                                )}
                                {displayInfo && (
                                    <div id="kc-info" className={getClassName("kcSignUpClass")}>
                                        <div id="kc-info-wrapper" className={getClassName("kcInfoAreaWrapperClass")}>
                                            {infoNode}
                                        </div>
                                    </div>
                                )}
                            </CardContent>
                        </Card>
                    </div>
                </div>
            </div>
        </div>
    );
}
